'use client';

import { createContext, useContext, useEffect, useState, ReactNode, useCallback } from 'react';

type Theme = 'dark' | 'light';

interface ThemeContextType {
    theme: Theme;
    toggleTheme: () => void;
    setTheme: (theme: Theme) => void;
    mounted: boolean;
}

const ThemeContext = createContext<ThemeContextType>({
    theme: 'dark',
    toggleTheme: () => { },
    setTheme: () => { },
    mounted: false,
});

export function ThemeProvider({ children }: { children: ReactNode }) {
    const [theme, setThemeState] = useState<Theme>('dark');
    const [mounted, setMounted] = useState(false);

    // Initialize on mount
    useEffect(() => {
        const saved = localStorage.getItem('smartlinkhub-theme') as Theme | null;
        if (saved && (saved === 'dark' || saved === 'light')) {
            setThemeState(saved);
        } else if (typeof window !== 'undefined' && window.matchMedia('(prefers-color-scheme: light)').matches) {
            setThemeState('light');
        }
        setMounted(true);
    }, []);

    // Apply theme to DOM
    useEffect(() => {
        if (!mounted) return;

        const root = document.documentElement;
        root.classList.remove('light-mode', 'dark-mode');
        root.classList.add(theme === 'light' ? 'light-mode' : 'dark-mode');

        // Also set data attribute for additional CSS hooks
        root.setAttribute('data-theme', theme);

        localStorage.setItem('smartlinkhub-theme', theme);

        console.log('[Theme] Applied:', theme);
    }, [theme, mounted]);

    const toggleTheme = useCallback(() => {
        setThemeState(prev => {
            const newTheme = prev === 'dark' ? 'light' : 'dark';
            console.log('[Theme] Toggling from', prev, 'to', newTheme);
            return newTheme;
        });
    }, []);

    const setTheme = useCallback((newTheme: Theme) => {
        setThemeState(newTheme);
    }, []);

    return (
        <ThemeContext.Provider value={{ theme, toggleTheme, setTheme, mounted }}>
            {children}
        </ThemeContext.Provider>
    );
}

export function useTheme() {
    return useContext(ThemeContext);
}
